// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import '@styles/theme/index.css'
import '@styles/main.scss'
import 'svg-innerhtml'
import ElementUI from 'element-ui'
import 'vue-awesome/icons'
import Icon from 'vue-awesome/components/Icon'
import Vue from 'vue'
import App from './App'
import router from './router'
import Resource from 'vue-resource'

Vue.config.productionTip = false

Vue.use(ElementUI)
Vue.use(Resource)
Vue.component('icon', Icon)

// icons
Icon.register({
  'devices': {
    width: 100,
    height: 90,
    raw: '<path xmlns="http://www.w3.org/2000/svg" d="M57.8,77.6l2-15.9c8.7-2,15.2-8.8,15.2-16.9V23.3h-6.6V4.7c0-2-1.9-3.6-4.2-3.6c-2.3,0-4.2,1.6-4.2,3.6v18.5H43.1V4.7   c0-2-1.9-3.6-4.2-3.6c-2.3,0-4.2,1.6-4.2,3.6v18.5h-7v21.5c0,8.2,6.6,15.1,15.5,17l1.6,15.9H57.8z"/>'
  }
})


/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  template: '<App/>',
  components: { App }
})



