import Vue from 'vue'
import Router from 'vue-router'
// import Login from '@src/components/Login'
import Dashboard from '@src/components/Dashboard'
import Auth from '@components/Auth'

// Import subroutes Dashboard
import Home from '@components/Dashboard/Home'
import Spotify from '@components/Dashboard/Spotify'
import Devices from '@components/Dashboard/Devices'

Vue.use(Router)


const router =  new Router({
  // mode: 'history',
  routes: [
    // {
    //   path: '/login',
    //   name: 'Login',
    //   component: Login
    // },
    {
      path: '',
      component: Dashboard,
      children: [
       {
         path: '',
         name:'Home',
         component: Home
       },
       {
         path: 'spotify',
         name: 'Spotify',
         component: Spotify
       },
       {
         path: 'devices',
         name: 'Devices',
         component: Devices
       }
     ]
   },
   {
     path: '/auth',
     name: 'Auth',
     component: Auth,
   }
  ]
})

router.beforeEach(async (to, from, next) => {
  try{
  var auth = await localStorage.getItem('auth');
  if(!auth && to.name !== 'Auth'){
    next('/auth')
  }
  else{
    if(!window.Homey && auth){
      console.log('Auth found, but no Homey object!');
      if(process.env.NODE_ENV === 'development'){
        var result = await router.app.$http.get('http://192.168.2.72/api/app/com.swttt.homeydash/auth?token=' + auth);
      }
      else{
        var result = await router.app.$http.get('/api/app/com.swttt.homeydash/auth?token=' + auth);
      }
          var Homey = {};
          if(process.env.NODE_ENV === 'development'){
            Homey = await new AthomAPI.HomeyAPI({
                            baseUrl: process.env.URL,
                            token: process.env.BEARER_TOKEN,
                            online: true,
                        });
          }
          else{
            Homey = await AthomAPI.HomeyAPI.forCurrentHomey(result.body.result);
          }
          window.Homey = await Homey;
          next();
      }
      else{
        next();
      }
  }
}
catch(error){
  console.error(error);
  router.app.$notify.error({
    title: 'Error',
    message: error.statusText
  });
}
})

export default router;



