<template>
<el-row>
  <el-col :span="10" :offset="7">
    <el-card class="box-card" style="margin-top:20%;">
      <center><img class="logo" src="~@assets/logo.svg" /></center>

      <el-form label-position="top" ref="form" :model="form" label-width="120px">
        <p>
          You need to authorize this client first to use Homeydash, you can find the authorization code in the app settings.
        </p>
        <el-form-item>
          <el-input size="large" style="font-size:24px;" type="text" v-model="form.token" placeholder="Authorization code"></el-input>
        </el-form-item>
        <el-button @click="auth(form.token)" style="width:100%;margin-top:15px;" type="primary">Authorize</el-button>
      </el-form>

    </el-card>
  </el-col>
</el-row>
</template>

<script>


export default {
  data() {
    return {
      form: {}
    }
  },
  mounted() {

  },
  methods: {
    async auth(code) {
      try{
        if(process.env.NODE_ENV === 'development'){
          var result = await this.$http.get('http://192.168.2.72/api/app/com.swttt.homeydash/auth?token=' + code);
        }
        else{
          var result = await this.$http.get('/api/app/com.swttt.homeydash/auth?token=' + code);
        }
        let token = await result.body.result;
        var Homey = {};
        if(process.env.NODE_ENV === 'development'){
          Homey = await new AthomAPI.HomeyAPI({
                          baseUrl: process.env.URL,
                          token: process.env.BEARER_TOKEN,
                          online: true,
                      });
        }
        else{
          Homey = await AthomAPI.HomeyAPI.forCurrentHomey(token);
        }
        window.Homey = Homey;

        localStorage.setItem('auth', code);
        this.$router.push({ path: '/' })
        this.$notify.success({
          title: 'Authorized',
          message: 'Authorized succesfully.'
        });
      }
      catch(error){
        this.$notify.error({
          title: 'Error',
          message: error
        });
        console.log(error);
      }
    }
  }
}
</script>



