<template>
  <div v-loading.fullscreen.lock="loading">
  <h1>{{zones}}</h1>
</div>
</template>

<script>
export default {
  data() {
    return {
      zones: {},
      loading: true
    }
  },
  components: {

  },
  async mounted() {
    this.zones = await window.Homey.zones.getZones({recursive: true});
    this.loading = false;
  },
  methods:{

  }
}
</script>



