<template>
  <div v-loading.fullscreen.lock="loading">
  <h1>{{info}}</h1>
</div>
</template>

<script>
export default {
  data() {
    return {
      info: {},
      loading: true
    }
  },
  components: {

  },
  async mounted() {
    await this.getInfo();
    this.loading = false;
  },
  methods:{
    async getInfo(){
      console.log(window.Homey)
      let info = await window.Homey.system.getInfo();
      this.info = info;
    }
  }
}
</script>



